@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css?family=Permanent+Marker);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
}

.App-header-inner {
  max-width: 1200px;
  width: 100%;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.disable-select {
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}
.HomePageDiv {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}

.HomePageTextArea {
    resize: none;
    outline: none;
    border: none;
    background-color: transparent;
    color: #292929;
    font-family: 'Lato', sans-serif; 
    width: 100%;
    padding-right: 20px;
}

.HomePageToolBarTextAreaDiv {
    flex-grow: 2;
    margin-left: 20px;
}
.ColorMapEditorDiv {
    position: relative;
}
.ColorMapDisplayDiv {
    border-radius: 15px;
    height: 80px;
    margin-left: 8px;
    margin-right: 8px;
    margin-bottom: 20px;
    margin-top: 20px;
    box-shadow:  8px 8px 16px #cccccc, 
    -8px -8px 16px #ffffff;
    cursor: copy;
}
.ColorMapSliderDiv {
    margin-left: 8px;
    margin-right: 8px;
    margin-top: -28px;
    z-index: 2;
}

.rc-slider-rail {
    display: none;
}

.rc-slider-track {
    display: none;
}
.ColorMapColorPickerOuterDiv {
    display: flex;
    justify-content: center;
    align-items: center;
}
.ColorMapColorPickerDiv {
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 50px;
    max-width: 800px;
}
.ColorMapAnchorListDiv {
    flex-grow: 2;
    min-width: 300px;
    max-width: 500px;
    height: 385px;
    overflow-y: auto;
}

.ColorMapAnchorListItem {
    display: flex;
    flex-direction: row;
    padding: 7px;
}

.ColorMapAnchorListName {
    flex-grow: 2;
    color: #292929;
    cursor: pointer;
    text-align: left;
    padding-left: 10px;
    display: flex;
    flex-direction: row;
    align-items: center;
    font-family: 'Lato', sans-serif;
}

.ColorMapAnchorListRemoveButtonDiv {
    width: 50px;
    color: #292929;
    cursor: pointer;
}

.ColorMapAnchorListColor {
    border-radius: 8px;
    height: 30px;
    width: 30px;
    margin-right: 10px;
}
.ViewPageDiv {

}

.ViewPageToolBarDiv {
    display: flex;
    flex-direction: row;
}

.ViewPageToolBarNameDiv {
    flex-grow: 2;
    text-align: left;
    padding-left: 30px;
    color: #292929;
    font-family: 'Lato', sans-serif; 
}

.ViewPageToolBarForkButtonDiv {
    margin-right: 20px;
}

.ViewPageToolBarSpinnerDiv {
    padding-right: 20px;
}
.NavBarDiv{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: 50px 0px 30px 0px;
}

.NavBarTitleDiv {
    width: auto;
    font-family: 'Permanent Marker', cursive;
    font-size: 3rem;
    color: #292929;
}
